import React, {useEffect, useState} from 'react';
import './App.css';
import colorArray from './colorArr'

let quoteDB="https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json";
function App() {
  const [quote,setQuote]=useState("When I stand before God at the end of my life, I would hope that I would not have a single bit of talent left and could say, I used everything you gave me.");
  const [author,setAuthor]=useState("Erma Bombeck")
  
  const [quotesArray,setQuotesArray]=useState(null)
  const [currentColor,setCurrentColor]=useState('#282c34')


  const fetchQuotes =async(url)=>{
    const response=await fetch(url)
    const parsed= await response.json()
    setQuotesArray(parsed.quotes)
    console.log(parsed)
  }
  
  useEffect(()=>{
    fetchQuotes(quoteDB)
    }
  ,[quoteDB])
  
  const getRandomQuote=()=>{
    let randomInteger=Math.floor(quotesArray.length*Math.random());
    setCurrentColor(colorArray[randomInteger % colorArray.length])
    setQuote(quotesArray[randomInteger].quote)
    setAuthor(quotesArray[randomInteger].author)
  }

  
  return (
    <div className="App">
      <header className="App-header" style={{backgroundColor: currentColor}}>
         <div id="quote-box" style={{color:currentColor}}>
          <p id="text">"{quote}" </p>
          <p id="author">- {author}</p>
          <button id= "new-quote" style={{backgroundColor: currentColor}} onClick={()=>getRandomQuote()}>
          New quote</button>
        <a id="tweet-quote" style={{backgroundColor: currentColor}} href={encodeURI("http://twitter.com/intent/tweet?text=${quote} -${author}")}>Tweet Quote</a>
      </div>
      </header>


    </div>
  );
}

export default App;
